body {
  font-family: DM Sans, sans-serif;
  font-size: 21px;
  line-height: 1.8;
}

.button {
  color: #fff;
  background-color: coral;
  border: 1px solid #000;
  border-radius: 10px;
  padding: 1em 2em;
}

.button:hover {
  background-color: #0ff;
}

.modal {
  z-index: 1;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 1, 0, .2);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  width: 579px;
  height: 465px;
  background-color: #fff;
  border: 2px solid #111;
  border-radius: 18px;
  margin: 20% auto;
  padding: 20px;
}

.close {
  color: #000;
  float: right;
  font-size: 28px;
}

.close:hover, .close:focus {
  color: #4f2ee8;
  cursor: pointer;
  text-decoration: none;
}

.about-book {
  display: -webkit-box;
}

.book-title {
  font-size: 24px;
  font-weight: bold;
  line-height: 28px;
}

.author-name {
  color: #b4afaf;
  font-size: 14px;
  font-style: italic;
  line-height: 18px;
}

.book-descr {
  font-size: 14px;
  font-style: normal;
  line-height: 18px;
}

.add-button, .remove-button {
  width: 499px;
  color: #111;
  background-color: #fff;
  border: 2px solid #4f2ee8;
  border-radius: 40px;
  padding: 20px;
  font-size: 18px;
  line-height: 24px;
}

.remove-button {
  display: none;
}

